<template>
  <div class="form-con">
    <h2 class="title">
      {{$route.name == '修改推广链接' ? '修改推广链接' : '创建推广链接'}}
    </h2>
    <Form :model="form" class="form-group" label-position="right" :label-width="100">
      <FormItem label="id" prop="id">
        <Input class="inputtxt" size="large" type="text" v-model="form.id" placeholder="请输入id"></Input>
      </FormItem>
      <FormItem label="标题" prop="title">
        <Input class="inputtxt" size="large" type="text" v-model="form.title" placeholder="请输入标题"></Input>
      </FormItem>
      <FormItem label="链接" prop="link">
        <Input class="inputtxt" size="large" type="text" v-model="link" placeholder="请输入链接"></Input>
        <Button style="margin-left: 20px;" @click="addOne" type="success" size="large">添加</Button>
      </FormItem>
      <FormItem label="链接组" prop="links">
        <ul>
          <li v-for="(item, index) in form.links" :key="index">
            <span>{{item}}</span>
            <Button type="error" size="large" @click="deleteLink(index)">X</Button>
          </li>
        </ul>
      </FormItem>
      <FormItem>
          <Button v-if="$route.name == '创建推广链接'" class="edit-btn" size="large" type="primary" @click="create">立即创建</Button>
          <Button v-else class="edit-btn" size="large" type="primary" @click="save">保 存</Button>
          <Button class="edit-btn" size="large" type="warning" @click="cancel">取 消</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
          links: []
      },
      link: '',
    }
  },
  mounted() {
    console.log(this.$route)
    if(this.$route.name == '修改推广链接') {
      this.form = this.$route.params.row
    }
  },
  methods: {
    deleteLink(index) {
      this.$Modal.confirm({
        title: '提示:',
        content: '确认删除这条内容吗？',
        onOk: () => {
          this.form.links.splice(index, 1);
        }
      })
    },
    addOne() {
      if (this.link == "") {
        this.$Message.info("链接不能为空");
      } else {
        this.form.links.push(this.link);
        this.link = "";
      }
    },
    save() {
      this.$axios.post('/transfer/update', this.form)
      .then(res => {
        if (res.data.success) {
          this.$Message.info(res.data.success);
          this.$router.push({
            name: '推广链接',
            params: {
              row: this.form,
              index: this.$route.params.index
            }
          })
          this.form = { links: [] };
        } else {
          this.$Message.info(res.data.err);
        }
      });
    },
    create() {
      this.$axios.post('/transfer/create', this.form)
      .then(res => {
        if (res.data.success) {
          this.$Message.info(res.data.success);
          this.$router.push({
            name: '推广链接',
            params: res.data.data
          })
          this.form = { links: [] };
        } else {
          this.$Message.info(res.data.err);
        }
      });
    },
    cancel() {
      this.$router.push({
        name: '推广链接',
        params: {state: 'cancel'}
      })
      this.form = { links: [] };
    }
  }
}
</script>
<style scoped>
.form-con {
  width: 900px;
  padding-top: 20px;
  margin: 0 auto;
  text-align: left;
}
.title {
  font-size: 36px;
  text-align: center;
  margin-bottom: 30px;
}
.inputtxt {
  width: 400px;
}
ul {
  list-style: none;
  overflow: hidden;
}
li {
  float: left;
  margin: 0 20px 20px 0;
}
li span {
  display: inline-block;
  padding: 0 15px;
  border: 1px solid #eee;
  border-radius: 5px;
  margin-right: 10px;
}

.edit-btn {
  margin-right: 20px;
}
</style>
